.padding-container {
    padding-top: 100px;
}
  
.login-container {
    border-radius: 10px;
    margin: 0 auto;
    width: 600px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    box-shadow: 2px 3px 14px 1px rgba(0,0,0,0.75);
}
  
.section-title {
    width: 100%;
    height: 120px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
  
.section-title--title {
    font-size: 45px;
    color: #1c004a;
}
  
.section-title-text {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.section-options {
    width: 100%;
    height: 170px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

  
.right {
    width: 50%;
    height: 160px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  
}

.div-social-buttons {
    width: 48%;
    height: 160px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-right: 2px dotted #cacaca;
}
  
.register-inputs {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
  
.right-input {
    width: 200px;
    border-radius: 5px;
    border: 1px solid #507cc0;
}
    
.center{
    width: 15%;
    height: 160px;
    display: flex;
    align-items: center;
    justify-content: center;
}
  
.btnLogin {
    width: 200px;
    padding: 5px 0;
    border-radius: 5px;
    background-color: #1c004a;
    color: #FFF;
    border: none;
}
  
.btnLogin:hover {
    color: #1c004a !important;
    background-color: #3ad8c1;
}
  
  
.loginButton{
    width: 160px;
    height: 35px;
    padding: 5px 10px;
    border-radius: 5px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    cursor: pointer;
}
  
.logo-footer-container {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: #1c004a;
}



@media (min-width: 280px) and (max-width:767px){
   
    .login-container {
        width: 80%;
        height: 550px;
    }

    .section-title {
        padding: 20px 0;
    }
   
    .section-title--title {
        font-size: 25px;
    }

    .section-options {
        height: 400px;
        display: flex;
        flex-direction: column-reverse;
        justify-content: space-around;
        align-items: center;
    }

    .right {
        width: 70%;
        height: 135px;
        padding-bottom: 30px;
        border-bottom: 1px dotted #929292;
    }

    .div-social-buttons {
        width: 100%;
        height: 115px;
        border-right: none;
    }

    .loginButton{
        width: 200px;
    }
}